.container {
  display: flex;
  padding: 10px;
  flex-wrap: wrap;
}
.temp-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  height: 100px;
  width: 100px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}
.temp-item .i-select {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #1890ff;
}
.temp-item:hover {
  color: #1890ff;
  background-color: #BBDEFB;
}
